import { addParamsForUrl, createUrl } from '@/lib/url';

export const GET = async (path, params = {}, options = {}) => {
  const urlWithParams = addParamsForUrl(path, params);
  const resp = await fetch(urlWithParams, {
    method: 'GET',
    ...options,
    withCredentials: true,
    credentials: 'include',
    headers: {
      'Content-Type': 'application/json'
    }
  });
  if (resp.ok) {
    return await resp.json();
  }
  return {
    error: {
      all: 'Чтото пошло не так'
    }
  };
};

export const POST = async (path, data = {}, options = {}) => {
  const url = createUrl(path);
  const resp = await fetch(url, {
    method: 'POST',
    body: JSON.stringify(data),
    ...options,
    withCredentials: true,
    credentials: 'include',
    headers: {
      'Content-Type': 'application/json'
    }
  });
  if (resp.ok) {
    return await resp.json();
  }
  return {
    error: {
      all: 'Чтото пошло не так'
    }
  };
};
