import {
  fetchLogin,
  fetchRegister,
  fetchLogout,
  fetchCheckAuth
} from '@/services/authServices';

const state = {
  user: null,
  error: {},
  load: false
};

const mutations = {
  SET_USER: (state, user) => {
    state.user = user;
  },
  SET_ERROR: (state, error) => {
    state.error = error;
  },
  SET_LOAD: (state, value) => {
    state.load = value;
  }
};

const actions = {
  async login({ commit }, data) {
    commit('SET_LOAD', true);
    const resp = await fetchLogin(data);
    if (resp.error) {
      commit('SET_ERROR', resp.error);
    } else {
      commit('SET_ERROR', {});
      commit('SET_USER', resp.user);
    }
    commit('SET_LOAD', false);
  },
  async registration({ commit }, data) {
    commit('SET_LOAD', true);
    const resp = await fetchRegister(data);
    if (resp.error) {
      commit('SET_ERROR', resp.error);
    } else {
      commit('SET_ERROR', {});
      commit('SET_USER', resp.user);
    }
    commit('SET_LOAD', false);
  },
  async checkAuth({ commit }) {
    const resp = await fetchCheckAuth();
    commit('SET_USER', resp.user);
  },
  async logout({ commit }) {
    const resp = await fetchLogout();
    if (resp.error) {
      commit('SET_ERROR', resp.error);
    } else {
      commit('SET_ERROR', {});
      commit('SET_USER', null);
      return resp.success;
    }
  }
};

const getters = {
  isAuthentication: state => !!state.user
};

export default { state, mutations, actions, getters };
