const state = {
  wallet: {
    name: '',
    status: 'Неакивно',
    balance: null,
    type: null
  }
};

const mutations = {
  SET_WALLET_NAME: (state, name) => {
    state.wallet.name = name;
  },
  SET_WALLET_TYPE: (state, type) => {
    state.wallet.type = type;
  },
  SET_WALLET_BALANCE: (state, balance) => {
    state.wallet.balance = balance;
  }
};

const actions = {};

export default { state, mutations, actions };
