<template>
  <div :class="isOpenPortableSidebar && css.layout">
    <div
      :class="[css.wrapper, isOpenPortableSidebar && css.left]"
      v-click-outside="clickOutside"
    >
      <div :class="css.title">Himi</div>
      <ul :class="css.list">
        <li :class="css.item" v-for="link in links" :key="link.title">
          <UiLink
            :url="link.url"
            v-if="link.isShow"
            @click.native="changeLinePosition"
          >
            {{ link.title }}
          </UiLink>
        </li>
      </ul>
      <div :class="css.line" :style="{ top: positionLine }"></div>
      <div :class="css.exit" @click="userLogout">
        На Выход
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex';
import Link from '@/components/UI/Link';
import { AuthPage } from '@/router/routing';
import { NAVIGATOR_LINKS } from './utils';

import css from './Sidebar.css';
export default {
  name: 'Sidebar',
  data() {
    return {
      links: NAVIGATOR_LINKS,
      exitLink: AuthPage(),
      positionLine: null,
      css
    };
  },
  computed: {
    ...mapState(['isOpenPortableSidebar'])
  },
  methods: {
    ...mapMutations(['SET_IS_OPEN_PORTABLE_SIDEBAR']),
    ...mapActions(['logout']),
    changeLinePosition(e) {
      this.positionLine = `${e.target.offsetTop}px`;
      if (this.isOpenPortableSidebar) this.SET_IS_OPEN_PORTABLE_SIDEBAR(false);
    },
    clickOutside() {
      if (this.isOpenPortableSidebar) this.SET_IS_OPEN_PORTABLE_SIDEBAR(false);
    },
    async userLogout() {
      const resp = await this.logout();
      if (resp) {
        await this.$router.push(AuthPage());
      }
    }
  },
  mounted() {
    const activeLink = document.getElementsByClassName(
      'router-link-exact-active'
    );
    this.positionLine = `${activeLink[0].offsetTop}px`;
  },
  components: {
    UiLink: Link
  }
};
</script>
