import Vue from 'vue';
import Vuex from 'vuex';
import userStore from './userStore';
import walletStore from './walletStore';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    loading: false,
    isOpenPortableSidebar: false
  },
  mutations: {
    LOADING: (state, value) => {
      state.loading = value;
    },
    SET_IS_OPEN_PORTABLE_SIDEBAR: (state, value) => {
      state.isOpenPortableSidebar = value;
    }
  },
  actions: {},
  modules: {
    userStore,
    walletStore
  }
});
