import Vue from 'vue';
import VueRouter from 'vue-router';
import { DefaultLayout, NoSidebarLayout } from '@/config';
import {
  AuthPage,
  DashboardPage,
  TransactionsPage,
  BudgetPlannerPage,
  CreateWalletPage
} from './routing';

Vue.use(VueRouter);

const routes = [
  {
    path: DashboardPage(),
    name: 'DashboardPage',
    meta: { layout: DefaultLayout, requiresAuth: true },
    component: () =>
      import(/* webpackChunkName: "dashboard" */ '../pages/DashboardPage')
  },
  {
    path: AuthPage(),
    name: 'Auth',
    meta: { layout: NoSidebarLayout },
    component: () => import(/* webpackChunkName: "auth" */ '../pages/AuthPage')
  },
  {
    path: BudgetPlannerPage(),
    name: 'BudgetPlannerPage',
    meta: { layout: DefaultLayout, requiresAuth: true },
    component: () =>
      import(/* webpackChunkName: "auth" */ '../pages/BudgetPlannerPage')
  },
  {
    path: TransactionsPage(),
    name: 'TransactionsPage',
    meta: { layout: DefaultLayout, requiresAuth: true },
    component: () =>
      import(/* webpackChunkName: "auth" */ '../pages/TransactionsPage')
  },
  {
    path: CreateWalletPage(),
    name: 'CreateWalletPage',
    meta: { layout: DefaultLayout, requiresAuth: true },
    component: () =>
      import(/* webpackChunkName: "auth" */ '../pages/CreateWalletPage')
  },
  {
    path: '*',
    name: 'Page404',
    meta: { layout: NoSidebarLayout },
    component: () =>
      import(/* webpackChunkName: "page404" */ '../pages/Page404')
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router;
