import Cookies from 'js-cookie';
import { AuthPage } from './routing';

export const routerMiddleware = async (to, from, next, store) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (Cookies.get('authSession')) {
      if (store.state.userStore.user) {
        next();
      } else {
        await store.dispatch('checkAuth');
        if (store.getters.isAuthentication) {
          next();
        } else {
          next({
            path: AuthPage()
          });
        }
      }
    } else {
      next({
        path: AuthPage()
      });
    }
  } else {
    next();
  }
};
